<template>
  <ue-title type="caption" :text="item.id" weight="regular" color="grey-lighten-1" padding="x-2" margin="t-2"/>
  <ue-title color="primary" :text="item?.content?.headline || $t('Please update the headline')" padding="x-2"/>

  <ue-configurable-card
    :title="item.name"
    style="background-color: transparent;"
    elevation="0"
    hide-separator
    align-center-columns
    :items="[
      Object.entries(item.press_release_packages ?? item.pressReleasePackages ?? {}).reduce((acc, [key, value]) => {
        acc.push(value)
        return acc;
      }, []),
      // {
      //   'Content': languageFileNames,
      //   'Media': mediaFileNames,
      //   'Date': item.content.date || 'N/A',
      // },
      item.content.date || 'N/A',
      [
        item._price ?? 'N/A',
        // item._status ?? 'Draft'
      ],
      [
        item._status ?? 'Draft'
      ],
    ]"
    :actions="[
      {
        name: 'edit',
        icon: 'mdi-pencil',
        color: 'primary'
      }
    ]"
  >
    <template #[`segment.1`]="segmentScope"
      >
      <v-row v-for="p in segmentScope.data" :key="`package-${p.id}`" no-gutters>
        <v-col cols="6">
          {{ p.packageable.name }}
        </v-col>
        <v-col cols="6">
          {{ p.name }}
        </v-col>
      </v-row>
    </template>
    <template #[`segment.2`]="segmentScope"
      >
      <div class="h-100 d-flex flex-wrap align-center justify-space-between">
        {{ segmentScope.data }}
      </div>
    </template>
    <template #[`segment.3`]="segmentScope"
      >
      <div class="h-100 d-flex flex-wrap align-center justify-space-between">
        <ue-dynamic-component-renderer :subject="item.state_formatted"/>
        <!-- <v-chip class="text-caption" :color="item.paymentStatus == 'Partially Paid' ? 'warning' : (item.paymentStatus == 'Paid' ? 'success' : 'error')" variant="flat">
          {{ $t(item.paymentStatusTranslated) }}
        </v-chip> -->
      </div>
    </template>
    <template #[`segment.4`]="segmentScope"
      >
      <div class="h-100 d-flex flex-wrap align-center justify-space-between">
        <ue-dynamic-component-renderer :subject="item.payment_status_formatted"/>
      </div>
    </template>
    <template #[`segment.actions`]="segmentScope"
      >
      <!-- <slot name="actions"></slot> -->
      <div>
        <div class="d-flex flex-wrap ga-2 justify-sm-end ml-n2 ml-md-0">
          <template v-for="(rowAction, k) in rowActions" :key="k">
            <v-tooltip v-if="itemHasAction(item, rowAction)"
              :text="$t( rowAction.label ?? $headline(rowAction.name) )"
              location="top"
              >
              <template v-slot:activator="{ props }">
                <v-btn
                  v-bind="props"
                  :text="rowAction.forceLabel ? $t( rowAction.label ?? $headline(rowAction.name) ) : null"
                  :variant="rowAction.variant ?? 'elevated'"
                  :density="rowAction.density ?? (rowAction.forceLabel ? 'comfortable' : 'compact')"
                  :size="rowAction.size ?? (rowAction.forceLabel ? 'default' : 'default')"
                  :icon="rowAction.forceLabel ? null : (rowAction.icon ? rowAction.icon : '$' + rowAction.name)"
                  :color="rowAction.color ?? 'primary'"
                  :rounded="rowAction.forceLabel ? null : true"
                  @click="itemAction(item, rowAction)"
                />
              </template>
            </v-tooltip>
          </template>
        </div>
      </div>
    </template>
  </ue-configurable-card>

  <!-- <div class="w-33">
    <ue-filepond-preview :source="languageFiles" show-inline-file-name image-size="24" />
    <v-divider v-if="languageFiles.length" class="my-2"></v-divider>
    <ue-filepond-preview :source="mediaFiles" show-inline-file-name image-size="24" />
  </div> -->

</template>

<script>
  import { useTableIterator, makeTableIteratorProps, tableIterableEmits } from '@/hooks/table'
  import { makeFormatterProps } from '@/hooks/useFormatter';

  const { ignoreFormatters } = makeFormatterProps()

  export default{
    emits: tableIterableEmits,
    props:{
      ...makeTableIteratorProps(),
      ignoreFormatters
    },
    setup(props, context){
      return {
        ...useTableIterator(props, context)
      }
    },
    computed: {
      languageFiles(){
        __log(this.item.fileponds)
        let pattern = /content\.(\d+)_content/
        return this.item.fileponds.filter(v => v.role.match(pattern))
      },
      languageFileNames(){
        return this.languageFiles.map(v => this.getFileName(v)).join(', ')
      },
      mediaFiles(){
        return this.item.fileponds.filter(v => v.role.match(/press_release_image/))
      },
      mediaFileNames(){
        return this.mediaFiles.map(v => this.getFileName(v)).join(', ')
      }
    },
    methods: {
      getFileName(filepond){
        const parts = filepond.file_name.split('.');
        const file_name = parts.slice(0, parts.length - 1).join('.');
        const extension = parts[parts.length - 1];
        return __shorten(file_name, 10) + '.' + extension;
      }
    },
    created(){
      // __log(this.item)
      // __log(JSON.stringify(this.item.fileponds))
      // console.log('ConfigurableCardIterator created', this.item, this.headers, this.iteratorOptions, this.rowActions)
    }

  }
</script>
